'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  getAll: function getAll(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: limit, offset
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/teams?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  getTeam: function getTeam(data, callback) {
    // Authentication: none
    // Required Parameters: team
    // Optional Parameters: none
    if (!data.team) return callback('team is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/teams/".concat(data.team);
    request('GET', options, callback);
  }
};