'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  user: function user(data, callback) {
    // Authentication: user_read
    // Required Parameters: none
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/user";
    options.auth = data.auth;
    request('GET', options, callback);
  },
  userByID: function userByID(data, callback) {
    // Authentication: none
    // Required Parameters: userID
    // Optional Parameters: none
    if (!data.userID) return callback('userID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID);
    request('GET', options, callback);
  },
  usersByName: function usersByName(data, callback) {
    // Authentication: none
    // Required Parameters: users
    // Optional Parameters: none
    if (!data.users) return callback('users is required');
    if (Array.isArray(data.users)) data.users = data.users.toString();
    var params = {};
    params.login = data.users;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  userEmotes: function userEmotes(data, callback) {
    // Authentication: user_subscriptions
    // Required Parameters: userID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.userID) return callback('userID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/emotes");
    options.auth = data.auth;
    request('GET', options, callback);
  },
  checkSub: function checkSub(data, callback) {
    // Authentication: user_subscriptions
    // Required Parameters: userID, channelID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.userID) return callback('userID is required');
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/subscriptions/").concat(data.channelID);
    options.auth = data.auth;
    request('GET', options, callback);
  },
  follows: function follows(data, callback) {
    // Authentication: none
    // Required Parameters: userID
    // Optional Parameters: limit, offset, direction, sortby
    if (!data.userID) return callback('userID is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    if (data.direction) params.direction = data.direction;
    if (data.sortby) params.sortby = data.sortby;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/follows/channels?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  checkFollow: function checkFollow(data, callback) {
    // Authentication: none
    // Required Parameters: userID, channelID
    // Optional Parameters: none
    if (!data.userID) return callback('userID is required');
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/follows/channels/").concat(data.channelID);
    request('GET', options, callback);
  },
  followChannel: function followChannel(data, callback) {
    // Authentication: user_follows_edit
    // Required Parameters: userID, channelID
    // Optional Parameters: notifications
    if (!data.auth) return callback('auth is required');
    if (!data.userID) return callback('userID is required');
    if (!data.channelID) return callback('channelID is required');
    var params = {};
    if (data.notifications) params.notifications = data.notifications;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/follows/channels/").concat(data.channelID, "?").concat(querystring.stringify(params));
    options.auth = data.auth;
    request('PUT', options, callback);
  },
  unfollowChannel: function unfollowChannel(data, callback) {
    // Authentication: user_follows_edit
    // Required Parameters: userID, channelID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.userID) return callback('userID is required');
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/follows/channels/").concat(data.channelID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  },
  blockList: function blockList(data, callback) {
    // Authentication: user_blocks_read
    // Required Parameters: userID
    // Optional Parameters: limit, offset
    if (!data.auth) return callback('auth is required');
    if (!data.userID) return callback('userID is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.userID, "/blocks?").concat(querystring.stringify(params));
    options.auth = data.auth;
    request('GET', options, callback);
  },
  blockUser: function blockUser(data, callback) {
    // Authentication: user_blocks_edit
    // Required Parameters: sourceUserID, targetUserID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.sourceUserID) return callback('sourceUserID is required');
    if (!data.targetUserID) return callback('targetUserID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.sourceUserID, "/blocks/").concat(data.targetUserID);
    options.auth = data.auth;
    request('PUT', options, callback);
  },
  unblockUser: function unblockUser(data, callback) {
    // Authentication: user_blocks_edit
    // Required Parameters: sourceUserID, targetUserID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.sourceUserID) return callback('sourceUserID is required');
    if (!data.targetUserID) return callback('targetUserID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/users/".concat(data.sourceUserID, "/blocks/").concat(data.targetUserID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  },
  createVHS: function createVHS(data, callback) {
    // Authentication: viewing_activity_read
    // Required Parameters: identifier
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.identifier) return callback('identifier is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/user/vhs";
    options.auth = data.auth;
    options.form = {
      identifier: data.identifier
    };
    request('PUT', options, callback);
  },
  checkVHS: function checkVHS(data, callback) {
    // Authentication: user_read
    // Required Parameters: none
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/user/vhs";
    options.auth = data.auth;
    request('GET', options, callback);
  },
  deleteVHS: function deleteVHS(data, callback) {
    // Authentication: viewing_activity_read
    // Required Parameters: none
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/user/vhs";
    options.auth = data.auth;
    request('DELETE', options, callback);
  }
};