'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  // Usernames of people in chat in the specified channel
  chatters: function chatters(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.channelName) return callback('channelName is required');
    var options = {};
    options.url = "http://tmi.twitch.tv/group/user/".concat(data.channelName, "/chatters");
    request('GET', options, callback);
  },
  // Channels that are hosting the specified channelID
  hosts: function hosts(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "http://tmi.twitch.tv/hosts?include_logins=1&target=".concat(data.channelID);
    request('GET', options, callback);
  },
  // Who the specified channelID is hosting
  hosting: function hosting(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "http://tmi.twitch.tv/hosts?include_logins=1&host=".concat(data.channelID);
    request('GET', options, callback);
  },
  // Who a user is subbed to
  subsTo: function subsTo(data, callback) {
    // Authentication: user_subscriptions
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelName) return callback('channelName is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/users/".concat(data.channelName, "/tickets");
    options.auth = data.auth;
    request('GET', options, callback);
  },
  // Random stream
  randomStream: function randomStream(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: none
    if (typeof data === 'function') callback = data;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/random";
    request('GET', options, callback);
  },
  // Provides user data not returned in the documented endpoint
  getUser: function getUser(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.channelName) return callback('channelName is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/channels/".concat(data.channelName);
    request('GET', options, callback);
  },
  chatProperties: function chatProperties(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.channelName) return callback('channelName is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/channels/".concat(data.channelName, "/chat_properties");
    request('GET', options, callback);
  },
  // Channels subscription program details
  product: function product(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.channelID) return callback('channelName is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/channels/".concat(data.channelName, "/product");
    request('GET', options, callback);
  },
  panels: function panels(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: none
    if (!data.channelName) return callback('channelName is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/channels/".concat(data.channelName, "/panels");
    request('GET', options, callback);
  },
  playlist: function playlist(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://api.twitch.tv/api/playlists/channels/".concat(data.channelID);
    request('GET', options, callback);
  },
  // Followed channels that are hosting someone else
  followedHosting: function followedHosting(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: limit, offset
    if (!data.channelName) return callback('channelName is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/api/users/".concat(data.channelName, "/followed/hosting?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  // Games that a user follows
  followedGames: function followedGames(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: limit, offset
    if (!data.channelName) return callback('channelName is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/api/users/".concat(data.channelName, "/follows/games?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  // Live games that a user follows
  followedGamesLive: function followedGamesLive(data, callback) {
    // Authentication: none
    // Required Parameters: channelName
    // Optional Parameters: limit, offset
    if (!data.channelName) return callback('channelName is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/api/users/".concat(data.channelName, "/follows/games/live?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  // Check if a user follows a game
  checkFollowGame: function checkFollowGame(data, callback) {
    // Authentication: none
    // Required Parameters: channelName, game
    // Optional Parameters: limit, offset
    if (!data.channelName) return callback('channelName is required');
    if (!data.game) return callback('game is required');
    var params = {};
    params.name = data.game;
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/api/users/".concat(data.channelName, "/follows/games/isFollowing?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  badges: function badges(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: none
    if (typeof data === 'function') callback = data;
    var options = {};
    options.url = "https://badges.twitch.tv/v1/badges/global/display";
    request('GET', options, callback);
  },
  subBadges: function subBadges(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://badges.twitch.tv/v1/badges/channels/".concat(data.channelID, "/display");
    request('GET', options, callback);
  },
  recentChat: function recentChat(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://tmi.twitch.tv/api/rooms/".concat(data.channelID, "/recent_messages");
    request('GET', options, callback);
  },
  cs: function cs(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: none
    if (typeof data === 'function') callback = data;
    var options = {};
    options.url = "https://api.twitch.tv/api/cs";
    request('GET', options, callback);
  },
  csMaps: function csMaps(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: none
    if (typeof data === 'function') callback = data;
    var options = {};
    options.url = "https://api.twitch.tv/api/cs/maps";
    request('GET', options, callback);
  }
};