'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  badges: function badges(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: none
    if (!data.channelID) return callback('channelID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/chat/".concat(data.channelID, "/badges");
    request('GET', options, callback);
  },
  emoteSet: function emoteSet(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: emotesets
    var params = {};
    if (data.emotesets) params.emotesets = data.emotesets;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/chat/emoticon_images?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  emotes: function emotes(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: none
    if (typeof data === 'function') callback = data;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/chat/emoticons";
    request('GET', options, callback);
  }
};