'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  getPosts: function getPosts(data, callback) {
    // Authentication: Optionally any scope
    // Required Parameters: channelID
    // Optional Parameters: limit, cursor, comments
    if (!data.channelID) return callback('channelID is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.cursor) params.cursor = data.cursor;
    if (data.comments) params.comments = data.comments;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts?").concat(querystring.stringify(params));
    if (data.auth) options.auth = data.auth;
    request('GET', options, callback);
  },
  getPost: function getPost(data, callback) {
    // Authentication: Optional, any scope
    // Required Parameters: channelID, postID
    // Optional Parameters: comments
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    var params = {};
    if (data.comments) params.comments = data.comments;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "?").concat(querystring.stringify(params));
    if (data.auth) options.auth = data.auth;
    request('GET', options, callback);
  },
  createPost: function createPost(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, post
    // Optional Parameters: share
    if (data.content) data.post = data.content;
    if (!data.auth) return callback('auth is required');
    if (!data.post) return callback('post is required');
    if (!data.channelID) return callback('channelID is required');
    var params = {};
    if (data.share) params.share = data.share;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts?").concat(querystring.stringify(params));
    options.auth = data.auth;
    options.form = {
      content: data.post
    };
    request('POST', options, callback);
  },
  deletePost: function deletePost(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  },
  createReaction: function createReaction(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, emoteID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.emoteID) return callback('emoteID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/reactions?emote_id=").concat(data.emoteID);
    options.auth = data.auth;
    request('POST', options, callback);
  },
  deleteReaction: function deleteReaction(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, emoteID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.emoteID) return callback('emoteID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/reactions?emote_id=").concat(data.emoteID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  },
  getComments: function getComments(data, callback) {
    // Authentication: Optional, any scope
    // Required Parameters: channelID, postID
    // Optional Parameters: limit, cursor
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.cursor) params.cursor = data.cursor;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/comments?").concat(querystring.stringify(params));
    if (data.auth) options.auth = data.auth;
    request('GET', options, callback);
  },
  createComment: function createComment(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, comment
    // Optional Parameters: none
    if (data.content) data.comment = data.content;
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.comment) return callback('comment is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/comments");
    options.auth = data.auth;
    options.form = {
      content: data.comment
    };
    request('POST', options, callback);
  },
  deleteComment: function deleteComment(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, commentID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.commentID) return callback('commentID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/comments/").concat(data.commentID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  },
  createCommentReaction: function createCommentReaction(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, commentID, emoteID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.commentID) return callback('commentID is required');
    if (!data.emoteID) return callback('emoteID is required');
    var params = {};
    params.emote_id = data.emoteID;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/comments/").concat(data.commentID, "/reactions?emote_id=").concat(data.emoteID);
    options.auth = data.auth;
    request('POST', options, callback);
  },
  deleteCommentReaction: function deleteCommentReaction(data, callback) {
    // Authentication: channel_feed_edit
    // Required Parameters: channelID, postID, comment ID, emoteID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.postID) return callback('postID is required');
    if (!data.commentID) return callback('commentID is required');
    if (!data.emoteID) return callback('emoteID is required');
    var params = {};
    params.emote_id = data.emoteID;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/feed/".concat(data.channelID, "/posts/").concat(data.postID, "/comments/").concat(data.commentID, "/reactions?emote_id=").concat(data.emoteID);
    options.auth = data.auth;
    request('DELETE', options, callback);
  }
};