'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  channels: function channels(data, callback) {
    // Authentication: none
    // Required Parameters: query
    // Optional Parameters: limit, offset
    if (!data.query) return callback('query is required');
    var params = {};
    params.query = data.query;
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/search/channels?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  games: function games(data, callback) {
    // Authentication: none
    // Required Parameters: query
    // Optional Parameters: live
    if (!data.query) return callback('query is required');
    var params = {};
    params.query = data.query;
    if (data.live) params.live = data.live;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/search/games?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  streams: function streams(data, callback) {
    // Authentication: none
    // Required Parameters: query
    // Optional Parameters: limit, offset, hls
    if (!data.query) return callback('query is required');
    var params = {};
    params.query = data.query;
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    if (data.hls) params.hls = data.hls;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/search/streams?".concat(querystring.stringify(params));
    request('GET', options, callback);
  }
};